











































































































































import { Vue, Component } from 'vue-property-decorator';
import citiesConstant from '@/views/constants/cities';
import G2 from '@antv/g2';
import Board from 'components/board.vue';

@Component({
  components: {
    Board,
  },
})
export default class OccuptaionAdd extends Vue {
  form: any = {
    comment: '',
    emails: [{
      value: ''
    }],
  };

  citiesConstant: any = citiesConstant;

  dynamicTags: string[] = ['标签一', '标签二', '标签三'];

  inputVisible: boolean = false;
    
  inputValue: string = '';

  rules: object = {
    comment: [
      { required: true, message: '请输入留言内容', trigger: 'blur' },
    ],
  };

  onSubmit() {
    const ref: any = this.$refs.form;
    ref.validate((valid: boolean) => {
      if (valid) {
        // submit;
      }
      return false;
    });
  }

  mounted() {
    const data = [
      { genre: 'Sports', sold: 275 },
      { genre: 'Strategy', sold: 115 },
      { genre: 'Action', sold: 120 },
      { genre: 'Shooter', sold: 350 },
      { genre: 'Other', sold: 150 }
    ]; 

    const div: any = this.$refs['chart'];
    const chart = new G2.Chart({
      container: div,
      width: 230, 
      height: 150,
      padding: {
        top: 15,
        right: 10,
        bottom: 35,
        left: 35,
      }
    });

    chart.source(data);
    chart.line().position('genre*sold');
    chart.point().position('genre*sold').size(4).shape('circle').style({
      stroke: '#fff',
      lineWidth: 1
    });
    chart.render();
  }

  handleClose(tag: string) {
    this.dynamicTags.splice(this.dynamicTags.indexOf(tag), 1);
  };

  showInput() {
    this.inputVisible = true;
    this.$nextTick(() => {
      /*
      const ref: any = this.$refs.saveTagInput.$refs;
      ref.input.focus();
      */
    });
  };

  handleInputConfirm() {
    let inputValue = this.inputValue;
    if (inputValue) {
      this.dynamicTags.push(inputValue);
    }
    this.inputVisible = false;
    this.inputValue = '';
  }

  removeEmail(item: any) {
    var index = this.form.emails.indexOf(item)
    if (index !== -1) {
      this.form.emails.splice(index, 1)
    }
  };

  addEmail() {
    this.form.emails.push({
      value: '',
      key: Date.now()
    });
  };
}
